import jsonp from 'common/js/jsonp'
import {commonParams, options} from './config'

export function getSingerList() {
  const url = 'https://c.y.qq.com/v8/fcg-bin/v8.fcg'

  const data = Object.assign({}, commonParams, {
    channel: 'singer',
    page: 'list',
    key: 'all_all_all',
    pagesize: 100,
    pagenum: 1,
    hostUin: 0,
    needNewCode: 0,
    platform: 'yqq'
  })

  return jsonp(url, data, options)
}

export function getSingerDetail(singerId) {
  const url = 'http://txdna.cn:5000/leetcode/api/problems'

  // const data = Object.assign({}, commonParams, {
  //   hostUin: 0,
  //   needNewCode: 0,
  //   platform: 'yqq',
  //   order: 'listen',
  //   begin: 0,
  //   num: 80,
  //   songstatus: 1,
  //   singermid: singerId
  // })

  const data = {
    Host: 'txdna.cn:5000',
    format: 'json'
  }

  const option = {
    param: 'jsonCallback'
  }
  return jsonp(url, data, option)
}




